import React, { useState } from "react";
import "./styles.css";
import { Button } from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import axios from "axios";
import { useForm } from "react-hook-form";
import CircularProgress from '@material-ui/core/CircularProgress';

const App = () => {
  const { errors, register, handleSubmit } = useForm();
  const [labelValue, setLabelValue] = useState("");
  const [blnShowProgress, setBlnShowProgress] = useState(false);

  const onSubmit = (data) => {
    console.log(data);
    setLabelValue('');
    setBlnShowProgress(true);
    axios.post("/predictPost" , {'query' : data.inputfield}).then((res) => {
      console.log("response", res);
      if (res.data) {
        setLabelValue(res.data.group);
      }
      setBlnShowProgress(false);
    }, (error) => {
      console.log('error', error);
      setBlnShowProgress(false);
    });


  };

  const progressBar = () => {
    if(!blnShowProgress){
      return null;
    }
    return (<CircularProgress color="secondary" />);
  }
  return (
    <div className="App">
      <h1>NLP App</h1>
      <p>
        Enter issue description and app will tell you which group shall resolve
        it
      </p>
      <form onSubmit={handleSubmit(onSubmit)}>
        <TextField
          inputRef={register({ required: true })} // pattern: /^[A-Za-z0-9\s]+$/i
          name="inputfield"
          id="standard-full-width"
          label="Issue Description"
          style={{ margin: 8 }}
          placeholder="Enter Description here"
          fullWidth
          margin="normal"
          InputLabelProps={{
            shrink: true,
          }}
        />
        {errors.inputfield &&
          "Input cannot be empty"}
        <br />
        <Button color="primary" type="submit">
          Get Group
        </Button> 
        <br />
        {progressBar()}
      </form>

      <p>{labelValue}</p>
    </div>
  );
};

export default App;
